#portfolio-page {
    /* border: 15px solid white; */
    position: fixed;
    left: 0;
    top: 0;
    bottom: 100;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    /* align-content: flex-start; */
    background: -webkit-linear-gradient(bottom, rgba(40,44,52,0.8) 20%, rgba(16,29,55,0.90) 40%), -webkit-linear-gradient(bottom, rgba(76, 209, 55,0.76) 10%, rgba(76, 209, 55,0.1) 40%), url(/joeljames/static/media/portfol.f2f0322a.jpg) center;
    background: linear-gradient(0deg, rgba(40,44,52,0.8) 20%, rgba(16,29,55,0.90) 40%), linear-gradient(0deg, rgba(76, 209, 55,0.76) 10%, rgba(76, 209, 55,0.1) 40%), url(/joeljames/static/media/portfol.f2f0322a.jpg) center;
    background-size: cover;
    min-height: 100%;
    min-width: 100%;
    color: white;
    font-family: 'Abril Fatface', cursive;
    -webkit-transition: -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: transform 1s cubic-bezier(0.63, 0.21, 1,-0.01), -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    /* overflow-y: scroll; */
    z-index: 4;
}

#portfolio-page.hide {
    /* Comment the below line out to work on portfolio page */
    -webkit-transform: translate3d(0, 100vh, 0);
            transform: translate3d(0, 100vh, 0);
    -webkit-transition: -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    transition: transform 1s cubic-bezier(0.63, 0.21, 1,-0.01), -webkit-transform 1s cubic-bezier(0.63, 0.21, 1,-0.01);
    background-size: cover;
    
}

#portfolio-page.show {
    position: absolute;
    -webkit-transform: translate3d(0, 0vh, 0);
            transform: translate3d(0, 0vh, 0);
    /* overflow-y: scroll; */
    background-size: cover;
}


